<template>
  <div id="app">
    <GPHeader/>
    <g-p-menu/>
    <tags-panel :collections="gifCollections"/>

    <ItemsContainer :collections="gifCollections"/>
    <scroll-top/>
    <popup/>
  </div>
</template>

<script>
  import Gif from './components/gif';
  import GPHeader from './components/GP-Header';
  import TagsPanel from './components/Tag-Panel';
  import ItemsContainer from './components/Items-Container';
  import GPMenu from './components/Menu';
  import Popup from './components/Popup';
  import ScrollTop from './components/ScollTop';

  export default {
    name: 'app',
    components: {
      GPHeader,
      TagsPanel,
      ItemsContainer,
      GPMenu,
      Popup,
      ScrollTop,
    },
    data() {
      return {
        gifCollections: Gif.items,
      };
    },
  };
</script>

<style lang="scss">

  @import "assets/scss/fonts";

  .select {
    height: 0;
    font-size: 0;
  }

  #app {
    font-family: 'raleway', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>



